import React from "react";
import { useTheme } from "@material-ui/core/styles";
import { Button, Grid, Paper, useMediaQuery } from "@material-ui/core";

import departmentLogo from "../../../assets/Images/web.png";

import DepartmentsData from "./Data/DepartmentsData";
import "./css/Departments.css";

const Salbar = (props) => {
    const data = props.data;

    return (
        <Paper className="salbarContainer">
            <h2 className="salbarContainerTitle">{data.title}</h2>
            <div className="salbarContainerPlaceContainer">
                <div className="salbarContainerPlace">{data.place}</div>
                <div className="salbarContainerFloor">{data.floor}</div>
            </div>
            <p className="salbarTime">{data.time}</p>
            <Button
                className="salbarButton"
                onClick={() =>
                    window.open(
                        `${data.link}`,
                        "_blank" // <- This is what makes it open in a new window.
                    )
                }
            >
                Дэлгэрэнгүй
            </Button>
        </Paper>
    );
};

function Departments() {
    const theme = useTheme();
    const tablet = useMediaQuery(theme.breakpoints.up("lg"));
    const mobile = useMediaQuery(theme.breakpoints.up("md"));

    return (
        <Grid container id="departments" className="salbarMainContainer">
            {tablet ? (
                <>
                    <Grid item sm={2} className="salbarContainerLeft">
                        <Paper className="salbarContainer"></Paper>
                    </Grid>
                    <Grid item sm={8}>
                        <Grid
                            container
                            spacing={1}
                            className="salbarContainerMiddle"
                        >
                            {DepartmentsData.filter(
                                (DepartmentsDataFilter) =>
                                    DepartmentsDataFilter.id === 1
                            ).map((item, idx) => (
                                <Grid
                                    item
                                    lg={4}
                                    className="salbarMiddleCardContainer"
                                    key={idx}
                                >
                                    <Salbar data={item} />
                                </Grid>
                            ))}
                            <Grid
                                item
                                lg={4}
                                className="salbarMiddleCardContainer"
                            >
                                <Paper className="salbarMiddleLogoContainer">
                                    <img
                                        alt="gadna"
                                        src={departmentLogo}
                                        className="salbarMiddleLogo"
                                    />
                                </Paper>
                            </Grid>
                            {DepartmentsData.filter(
                                (DepartmentsDataFilter) =>
                                    DepartmentsDataFilter.id !== 1
                            ).map((item, idx) => (
                                <Grid
                                    item
                                    lg={4}
                                    className="salbarMiddleCardContainer"
                                    key={idx}
                                >
                                    <Salbar data={item} />
                                </Grid>
                            ))}
                        </Grid>
                    </Grid>
                    <Grid item sm={2} className="salbarContainerRight">
                        <Paper className="salbarContainer"></Paper>
                    </Grid>
                </>
            ) : mobile ? (
                <>
                    <Grid item sm={2} className="salbarContainerLeft">
                        <Paper className="salbarContainer"></Paper>
                    </Grid>
                    <Grid item sm={8}>
                        <Grid
                            container
                            spacing={1}
                            className="salbarContainerMiddle"
                        >
                            <Grid
                                item
                                sm={12}
                                lg={4}
                                className="salbarMiddleCardContainer"
                            >
                                <Paper className="salbarMiddleLogoContainer">
                                    <img
                                        alt="gadna"
                                        src={departmentLogo}
                                        className="salbarMiddleLogo"
                                    />
                                </Paper>
                            </Grid>
                            {DepartmentsData.map((item) => {
                                return (
                                    <Grid
                                        item
                                        sm={item.id === 5 ? 12 : 6}
                                        lg={4}
                                        className="salbarMiddleCardContainer"
                                        key={item.id}
                                    >
                                        <Salbar data={item} />
                                    </Grid>
                                );
                            })}
                        </Grid>
                    </Grid>
                    <Grid item sm={2} className="salbarContainerRight">
                        <Paper className="salbarContainer"></Paper>
                    </Grid>
                </>
            ) : (
                <>
                    <Grid item xs={12} sm={12} className="salbarContainerLeft">
                        <Paper className="salbarContainer"></Paper>
                    </Grid>
                    <Grid item xs={12} sm={12}>
                        <Grid
                            container
                            spacing={1}
                            className="salbarContainerMiddle"
                        >
                            <Grid
                                item
                                xs={12}
                                sm={12}
                                lg={4}
                                className="salbarMiddleCardContainer"
                            >
                                <Paper className="salbarMiddleLogoContainer">
                                    <img
                                        alt="gadna"
                                        src={departmentLogo}
                                        className="salbarMiddleLogo"
                                    />
                                </Paper>
                            </Grid>
                            {DepartmentsData.map((item) => {
                                return (
                                    <Grid
                                        item
                                        xs={12}
                                        sm={item.id === 5 ? 12 : 6}
                                        lg={4}
                                        className="salbarMiddleCardContainer"
                                        key={item.id}
                                    >
                                        <Salbar data={item} />
                                    </Grid>
                                );
                            })}
                        </Grid>
                    </Grid>
                    <Grid item xs={12} sm={12} className="salbarContainerRight">
                        <Paper className="salbarContainer"></Paper>
                    </Grid>
                </>
            )}
        </Grid>
    );
}

export default Departments;
